@mixin columnWidth( $selector ){
  &[data-column="1"]{
    #{$selector}{width:100%;}
  }
  &[data-column="2"]{
    #{$selector}{width:50%;}
  }
  &[data-column="3"]{
    #{$selector}{width:33.33333%;}
  }
  &[data-column="4"]{
    #{$selector}{width:25%;}
  }
  &[data-column="5"]{
    #{$selector}{width:20%;}
  }
  &[data-column="6"]{
    #{$selector}{width:16.6666667%;}
  }
  &[data-column="8"]{
    #{$selector}{width:12.5%;}
  }
}